"use client";

import React, { useState } from "react";
import DiagnosisCard from "@/components/DiagnosisCard";

interface DiagnosisData {
  disease: string;
  description: string;
  recommendations: string;
}

export default function SymptomChecker() {
  const [symptoms, setSymptoms] = useState("");
  const [loading, setLoading] = useState(false);
  const [diagnosis, setDiagnosis] = useState<DiagnosisData | null>(null);
  const [error, setError] = useState("");

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError("");
    setDiagnosis(null);

    if (!symptoms.trim()) {
      setError("Please enter your pet's symptoms to get a diagnosis.");
      return;
    }

    setLoading(true);
    try {
      const res = await fetch("/api/diagnose", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ symptoms }),
      });

      if (!res.ok) {
        const errorData = await res.json();
        throw new Error(errorData.message || "Diagnosis failed");
      }

      const data = await res.json();
      setDiagnosis(data);
    } catch (err: any) {
      setError(err.message || "An unexpected error occurred. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  const handleReset = () => {
    setSymptoms("");
    setDiagnosis(null);
    setError("");
  };

  return (
    <div className="container mx-auto px-6 py-8">
      <div className="max-w-4xl mx-auto space-y-8">
        <div className="text-center space-y-4">
          <h1 className="text-3xl md:text-4xl font-bold text-foreground">
            Pet Symptom Checker
          </h1>
          <p className="text-lg text-muted-foreground max-w-2xl mx-auto">
            Describe your pet's symptoms in detail to receive a preliminary assessment and care recommendations.
          </p>
        </div>

        <div className="bg-card border border-border rounded-lg p-6">
          <form onSubmit={handleSubmit} className="space-y-6">
            <div className="space-y-2">
              <label htmlFor="symptoms" className="block text-sm font-semibold text-card-foreground">
                Pet Symptoms
              </label>
              <textarea
                id="symptoms"
                className="w-full border border-border rounded-lg p-4 focus:outline-none focus:ring-2 focus:ring-primary focus:border-transparent resize-none bg-background text-foreground placeholder:text-muted-foreground"
                placeholder="Please describe your pet's symptoms in detail. For example: 'My dog has been coughing for 2 days, seems lethargic, and has a runny nose. He's also not eating as much as usual.'"
                value={symptoms}
                onChange={(e) => setSymptoms(e.target.value)}
                rows={6}
                disabled={loading}
              />
              <p className="text-xs text-muted-foreground">
                Be as specific as possible. Include duration, severity, and any other relevant details.
              </p>
            </div>

            <div className="flex gap-4">
              <button
                type="submit"
                disabled={loading || !symptoms.trim()}
                className="flex-1 px-6 py-3 bg-primary text-primary-foreground rounded-lg hover:bg-primary/90 transition-colors font-semibold disabled:opacity-50 disabled:cursor-not-allowed"
              >
                {loading ? "Analyzing Symptoms..." : "Get Diagnosis"}
              </button>
              
              {(diagnosis || error) && (
                <button
                  type="button"
                  onClick={handleReset}
                  className="px-6 py-3 bg-secondary text-secondary-foreground rounded-lg hover:bg-secondary/90 transition-colors font-semibold"
                >
                  Start Over
                </button>
              )}
            </div>
          </form>
        </div>

        {error && (
          <div className="bg-destructive/10 border border-destructive/20 rounded-lg p-4">
            <div className="flex items-start space-x-3">
              <div className="flex-1">
                <h3 className="font-semibold text-destructive mb-1">Error</h3>
                <p className="text-destructive/80">{error}</p>
              </div>
            </div>
          </div>
        )}

        {loading && (
          <div className="bg-card border border-border rounded-lg p-8 text-center">
            <div className="space-y-4">
              <div className="w-8 h-8 border-4 border-primary border-t-transparent rounded-full animate-spin mx-auto"></div>
              <p className="text-muted-foreground">Analyzing your pet's symptoms...</p>
            </div>
          </div>
        )}

        {diagnosis && (
          <div className="space-y-4">
            <h2 className="text-2xl font-bold text-foreground">Diagnosis Results</h2>
            <DiagnosisCard {...diagnosis} />
          </div>
        )}

        <div className="bg-muted rounded-lg p-6">
          <h3 className="font-semibold text-foreground mb-3">Important Guidelines:</h3>
          <ul className="space-y-2 text-sm text-muted-foreground">
            <li>• This tool provides preliminary guidance based on common symptoms</li>
            <li>• Always consult with a qualified veterinarian for proper diagnosis</li>
            <li>• In case of emergency symptoms, contact your vet immediately</li>
            <li>• Keep a record of symptoms to share with your veterinarian</li>
          </ul>
        </div>
      </div>
    </div>
  );
}
